          paragraph1:
            "Ma passion pour le dveloppement a commenc en 2016. J'ai dcouvert  cette poque le potentiel illimit de la programmation informatique et je suis immdiatement tomb amoureux !",
          paragraph2:
            "Jai ainsi dcid de changer radicalement de vie et de me consacrer  cette passion. Jai commenc  pratiquer en parallle d'emplois dans d'autres domaines, ce qui m'a demand un changement radical et une grande concentration.",
          paragraph3:
            "Cela ma permis dacqurir une exprience prcieuse sur divers projets de dveloppement. Ds lors, j'ai mis tout en oeuvre pour apprendre et russir dans cette voie.",
          paragraph4:
            "En Janvier 2023, j'ai dcid de me lancer et d'en faire mon mtier en passant le diplme Bac+2 de Developpeur Web et Web mobile 'Full Stack', que j'ai obtenu en Fvrier 2024.",
          paragraph5:
            "Aprs ce succs, le formateur React et Javascript de l'cole m'a offert la superbe opportunit de raliser un stage de plusieurs mois en Full remote chez Andragogy pour l'aider  dvelopper une application dans le secteur de la formation personnalise accompagne par l'intelligence artificielle destine aux entreprises et aux coles.",
